(function() {
  angular.module('blackfire').directive('appConnected', function() {
    var connectedController, directive;
    connectedController = function($state, $timeout) {
      'ngInject';
      var cn, playButtonSrc;
      cn = this;
      cn.page = $state.current.name;
      playButtonSrc = './assets/images/play-button.png';
      cn.playButton = playButtonSrc;
      cn.playing = false;
      cn.started = false;
      cn.data = {
        cta: {
          title: 'Always connected',
          description: {
            home: 'Enjoying a truly seamless real-time media experience on multiple devices has never been this easy. Our technology works with many hardware platforms',
            solution: 'What’s the point of wireless if you always need to pair your device? With Blackfire you can get the music going from any room.'
          }
        }
      };
      cn.toggleVideo = function(page) {
        var ref, video;
        if (page === 'home') {
          video = document.getElementById('connected-home-video');
        } else {
          video = document.getElementById('connected-solution-video');
        }
        video.muted = (ref = video.muted === 'muted') != null ? ref : {
          "false": 'muted'
        };
        cn.playing = !cn.playing;
        cn.started = true;
        if (cn.playing) {
          return video.play();
        } else {
          return video.pause();
        }
      };
    };
    return directive = {
      restrict: 'A',
      templateUrl: 'app/components/connected/connected.html',
      controller: connectedController,
      controllerAs: 'cn',
      bindToController: true
    };
  });

}).call(this);



