(function() {
  angular.module('blackfire').run(function($interval, $log, $rootScope, $timeout, $window) {
    'ngInject';
    var add, checkScroll, parallax, videoPlaying;
    $rootScope.toggleSearch = function() {
      $rootScope.onSearch = !$rootScope.onSearch;
      return $timeout(function() {
        return $('#search-input').focus();
      }, 300);
    };
    angular.element($window).bind("scroll", function() {
      $rootScope.pageYOffset = $window.pageYOffset;
      $rootScope.pageWidth = $window.innerWidth;
      $rootScope.$apply();
      return checkScroll();
    });
    $rootScope.global = {
      scrollTop: function() {
        return $rootScope.pageYOffset;
      },
      toggleMenu: function() {
        return $rootScope.showMenu = !$rootScope.showMenu;
      }
    };
    checkScroll = function() {
      $('.fade-in-up-item').each(function() {
        return add($(this), 'animated fadeInUp');
      });
      $('.fade-in-down-item').each(function() {
        return add($(this), 'animated fadeInDown');
      });
      $('.fade-in-item').each(function() {
        return add($(this), 'animated fadeIn');
      });
      $('.fade-in-left-item').each(function() {
        return add($(this), 'animated fadeInLeft');
      });
      $('.fade-in-right-item').each(function() {
        return add($(this), 'animated fadeInRight');
      });
      return $('#contact-banner').each(function() {
        return parallax();
      });
    };
    videoPlaying = true;
    $interval(function() {
      var scrY, video1, video2;
      video1 = document.getElementById('home-video-1');
      video2 = document.getElementById('home-video-2');
      scrY = $window.pageYOffset / $window.innerWidth;
      if (video1 && video2) {
        video1.muted = 'muted';
        video2.muted = 'muted';
        $('.app-slide .carousel-inner > .item').each(function() {
          if ($(this).hasClass('active')) {
            if ($(this).index() === 0) {
              if (scrY < 0.3 && !videoPlaying) {
                console.log(1);
                videoPlaying = true;
                video1.play();
                return video2.play();
              }
            } else {
              if (videoPlaying) {
                console.log(2);
                videoPlaying = false;
                video1.pause();
                return video2.pause();
              }
            }
          }
        });
        if (scrY > 0.3 && videoPlaying) {
          console.log(3);
          videoPlaying = false;
          video1.pause();
          return video2.pause();
        }
      }
    }, 10);
    add = function(self, cls) {
      var bottom_of_object, bottom_of_window;
      bottom_of_object = self.offset().top + self.outerHeight();
      bottom_of_window = $(window).scrollTop() + $(window).height();
      if (self.outerHeight() > 50) {
        bottom_of_object = self.offset().top + 50;
      }
      if (bottom_of_window > bottom_of_object) {
        return self.addClass(cls);
      }
    };
    parallax = function() {
      return $rootScope.parallaxY = ($rootScope.pageWidth * 0.43 + 50 - $rootScope.pageYOffset) / $rootScope.pageWidth * 43 - 20;
    };
    return $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {
      $rootScope.showMenu = false;
      window.scrollTo(0, 0);
      return $timeout(function() {
        return checkScroll();
      }, 300);
    });
  });

}).call(this);



